@import "../../base";

.horizontalbox {
  width: 390px;

  .content {
    display: flex;
    img {
      margin-right: 10px;
    }
  }

  .title {
    font-weight: bold;
    margin-bottom: 10px;
    text-decoration: none;
    color: #4a4a4a;

    &:hover {
      text-decoration: underline;
    }
  }

  .footer {
    margin-top: 5px;
    display: flex;
  }

  .button {
    background-color: #4a4a4a;
    border: 1px solid #000000;
    width: 90px;
    height: 22px;
    color: white;
    font-size: 14px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
